.far{
    color: #fff;
    font-size: 1.5em;
    margin: 0.5em;
}

#hero {
    width: 100%;
    height: calc(100vh - 110px);
    background: url('https://bootstrapmade.com/demo/templates/Day/assets/img/hero-bg.jpg') top center;
    background-size: cover;
    position: relative;
}
.scrolled-offset {
    margin-top: 70px;
}
.align-items-center {
    align-items: center!important;
}
.d-flex {
    display: flex!important;
}
section {
    padding: 60px 0;
    overflow: hidden;
}

#hero .overlay {
    content: "";
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
}
#hero  h1 {
    margin: 0 0 10px 0;
    font-size: 48px;
    font-weight: 700;
    line-height: 56px;
    text-transform: uppercase;
    color: #fff;
}
#hero h2 {
    color: #eee;
    margin-bottom: 30px;
    font-size: 24px;
}

a {
    color: #cc1616;
    text-decoration: none;
}


#hero .overlay .btn-get-started {
    font-family: "Raleway", sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 14px;
    letter-spacing: 0.5px;
    display: inline-block;
    padding: 10px 28px;
    transition: 0.5s;
    border: 2px solid #fff;
    color: #fff;
}
#hero .overlay .btn-get-started:hover {
    background: #cc1616;
    border-color: #cc1616;
}
@media (min-width: 1024px) {
    #hero {
        background-attachment: fixed;
    }
}
@media (max-width: 768px) {
    #hero {
        text-align: center;
    }
    #hero .overlay .container {
        padding-top: 40px;
    }
    #hero h1 {
        font-size: 28px;
        line-height: 36px;
    }
    #hero h2 {
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 30px;
    }
}


/* ==== GENERAL STYLES ==== */

* {
    box-sizing: border-box;
}

strong {
    font-weight: 700;
}

/* ==== SIMPLE FOOTER ==== */

footer {
    background: url('https://orange.codeberryschool.com/content/images/project-assets/simple-footer.png') repeat center;
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    height: 8rem;
    text-align: center;
    padding: 1rem;
    font-family: 'Arial', sans-serif;
    font-size: .875rem;
}

.wrapper {
    display: table-cell;
    vertical-align: middle;
}

footer small {
    color: #ccc;
    letter-spacing: .025rem;
    margin-bottom: 1.5rem;
    display: block;
}

.footer-nav a {
    color: #c74f78;
    text-decoration: none;
    margin: 0 .5rem;
    display: inline-block;
}

.footer-nav a:hover {
    color: #f26896;
    transition: color .15s ease-in-out;
}



